import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div class=\"navbar\">\n  {{#each @routes as |route|}}\n    <LinkTo @route={{route.route}}>\n      {{#if route.icon}}\n        <i class={{route.icon}}></i>\n      {{/if}}\n      {{route.name}}\n    </LinkTo>\n  {{/each}}\n</div>', { moduleName: 'dummy/templates/components/examples-nav-plain-html.hbs' });
