import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Filtering\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  <code>First name</code> uses column values for filter options.<br>\n  <code>Last name</code> has a predefined list of options.<br>\n  <code>Age</code> uses a custom filter-function combined with a predefined list of options.\n</p>\n{{! BEGIN-SNIPPET table-with-custom-filters }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-filters.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-filters.js\"/>\n  </div>\n</div>\n\n<h4>\n  Filtering\n  <small>server paginated table</small>\n</h4>\n<p class=\"alert alert-info\">\n  <code>First name</code> and <code>Last name</code> have a predefined lists of options.\n</p>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columnsForServerTable}}\n  @themeInstance={{this.fw.themeInstance}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}/>\n', { moduleName: 'dummy/templates/examples/filtering.hbs' });
