import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div class={{if @column.filterUsed \"has-feedback\"}}>\n  <ModelsTable::Themes::Default::Select\n    @options={{this.filterOptions}}\n    @cssPropertyName={{@column.cssPropertyName}}\n    @value={{@column.filterString}}\n    @changeValue={{this.onChangeColumnFilter}}\n    class=\"changeFilterForColumn {{@themeInstance.input}}\"/>\n  {{#if @column.filterUsed}}\n    <i\n      class=\"clearFilterIcon {{@themeInstance.clearFilterIcon}}\"\n      role=\"button\"\n      aria-label={{@themeInstance.clearFilterMsg}}\n      {{on \"click\" this.clearFilter}}>\n    </i>\n  {{/if}}\n</div>\n{{yield}}\n', { moduleName: 'dummy/templates/components/filter-cell-select.hbs' });
