import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Route cells\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  firstName cell is a link to the route for model in the row.\n</p>\n{{! BEGIN-SNIPPET table-with-route-cells }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-route-cells.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-route-cells.js\"/>\n  </div>\n</div>\n\n<h4>\n  Route cells\n  <small>server paginated table</small>\n</h4>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}\n  @doQuery={{this.doQuery}}/>\n', { moduleName: 'dummy/templates/examples/route-cells.hbs' });
