import { hbs } from 'ember-cli-htmlbars';
export default hbs('<button {{on \"click\" this.doToggleGroupedRows}} type=\"button\" class=\"toggle-grouped-group\">\n  {{@currentGroupingPropertyName}}:\n  {{@groupedValue}}\n  ({{@visibleGroupedItems.length}}).\n  {{if @groupIsCollapsed \"collapsed\" \"expanded\"}}\n</button>\n<button\n  type=\"button\"\n  class=\"toggle-selection-group\"\n  {{on \"click\" this.doToggleGroupedRowsSelection}}>\n</button>\n<button\n  type=\"button\"\n  class=\"toggle-expands-group\"\n  {{on \"click\" this.doToggleGroupedRowsExpands}}>\n</button>\n<span class=\"expanded-count\">{{@expandedGroupedItems.length}}</span>\n<span class=\"selected-count\">{{@selectedGroupedItems.length}}</span>', { moduleName: 'dummy/templates/components/custom-row-group-toggle.hbs' });
