import { hbs } from 'ember-cli-htmlbars';
export default hbs('<this.BsNav @type=\"tabs\" as |nav|>\n  {{#each @routes as |route|}}\n    <nav.item>\n      {{#nav.link-to route.route}}\n        {{#if route.icon}}\n          <i class={{route.icon}}></i>\n        {{/if}}\n        {{route.name}}\n      {{/nav.link-to}}\n    </nav.item>\n  {{/each}}\n</this.BsNav>', { moduleName: 'dummy/templates/components/examples-nav-bs4.hbs' });
