import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Common table\n  <small>simple table</small>\n</h4>\n\n<p>This options are applied for both tables:</p>\n{{component\n  (ensure-safe-component this.settingsComponent)\n  showComponentFooter=this.showComponentFooter\n  showColumnsDropdown=this.showColumnsDropdown\n  useFilteringByColumns=this.useFilteringByColumns\n  showGlobalFilter=this.showGlobalFilter\n  useNumericPagination=this.useNumericPagination\n  doFilteringByHiddenColumns=this.doFilteringByHiddenColumns\n  filteringIgnoreCase=this.filteringIgnoreCase\n  multipleColumnsSorting=this.multipleColumnsSorting\n  showPageSize=this.showPageSize\n  collapseNumPaginationForPagesCount=this.collapseNumPaginationForPagesCount\n  showCurrentPageNumberSelect=this.showCurrentPageNumberSelect\n}}\n\n{{! BEGIN-SNIPPET common-table }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @showComponentFooter={{this.showComponentFooter}}\n  @showColumnsDropdown={{this.showColumnsDropdown}}\n  @useFilteringByColumns={{this.useFilteringByColumns}}\n  @showGlobalFilter={{this.showGlobalFilter}}\n  @doFilteringByHiddenColumns={{this.doFilteringByHiddenColumns}}\n  @useNumericPagination={{this.useNumericPagination}}\n  @filteringIgnoreCase={{this.filteringIgnoreCase}}\n  @multipleColumnsSorting={{this.multipleColumnsSorting}}\n  @showCurrentPageNumberSelect={{this.showCurrentPageNumberSelect}}\n  @collapseNumPaginationForPagesCount={{this.collapseNumPaginationForPagesCount}}\n  @currentPageNumber={{this.currentPageNumber}}\n  @showPageSize={{this.showPageSize}}\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}/>\n{{! END-SNIPPET }}\n\n<CodeBlockExample @snippetName=\"common-table.hbs\"/>\n\n<h4>\n  Common table\n  <small>server paginated table</small>\n</h4>\n{{! BEGIN-SNIPPET common-table-server-paginated }}\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @filterQueryParameters={{this.filterQueryParameters}}\n  @showComponentFooter={{this.showComponentFooter}}\n  @showColumnsDropdown={{this.showColumnsDropdown}}\n  @useFilteringByColumns={{this.useFilteringByColumns}}\n  @showGlobalFilter={{this.showGlobalFilter}}\n  @useNumericPagination={{this.useNumericPagination}}\n  @showCurrentPageNumberSelect={{this.showCurrentPageNumberSelect}}\n  @collapseNumPaginationForPagesCount={{this.collapseNumPaginationForPagesCount}}\n  @showPageSize={{this.showPageSize}}\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}/>\n{{! END-SNIPPET }}\n<CodeBlockExample @snippetName=\"common-table-server-paginated.hbs\"/>', { moduleName: 'dummy/templates/examples/common-table.hbs' });
