import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  In-line edit. Example of using provided cell-edit-toggle\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  Some records may be deleted from both tables in the same time.\n</p>\n{{! BEGIN-SNIPPET table-with-inline-edit }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @columnComponents={{hash\n    editRow=(\n      component\n      (ensure-safe-component this.cellEditToggleComponent)\n      onSaveRow=this.onSaveRow\n      onCancelRow=this.onCancelRow\n  )\n  }}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-inline-edit-controller.js\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-inline-edit.hbs\"/>\n    <CodeBlockExample @snippetName=\"table-with-inline-edit-route.js\"/>\n  </div>\n</div>\n\n<h4>\n  In-line edit\n  <small>server paginated table</small>\n</h4>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}\n  @columnComponents={{hash\n    editRow=(\n      component\n      (ensure-safe-component this.cellEditToggleComponent)\n      onSaveRow=this.onSaveRow\n      onCancelRow=this.onCancelRow\n    )\n  }}/>\n', { moduleName: 'dummy/templates/examples/in-line-edit.hbs' });
