import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>Column sets <small>simple table</small></h4>\n{{! BEGIN-SNIPPET table-with-column-sets }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @columnSets={{this.columnSets}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-column-sets.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-column-sets.js\"/>\n  </div>\n</div>\n\n<h4>Columns sets <small>server paginated table</small></h4>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @columnSets={{this.columnSets}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}/>', { moduleName: 'dummy/templates/examples/column-sets.hbs' });
