import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Custom classes for columns\n</h4>\n<p class=\"alert alert-info\">\n  Setting the <code>className</code> property on a column adds a css class to each cell in a column. This can be useful for styling columns.\n</p>\n{{! BEGIN-SNIPPET table-with-custom-column-classes }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}/>\n{{! END-SNIPPET }}\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.hbs\" />\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.scss\" />\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.js\" />\n  </div>\n</div>', { moduleName: 'dummy/templates/examples/custom-column-classes.hbs' });
