import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showComponentFooter\"\n      @checked={{this.showComponentFooter}}/> Show Footer (<code>showComponentFooter</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showColumnsDropdown\"\n      @checked={{this.showColumnsDropdown}}/> Show Columns Dropdown (<code>showColumnsDropdown</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"useFilteringByColumns\"\n      @checked={{this.useFilteringByColumns}}/> Use filtering by columns (<code>useFilteringByColumns</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showGlobalFilter\"\n      @checked={{this.showGlobalFilter}}/> Show global filter (<code>showGlobalFilter</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"useNumericPagination\"\n      @checked={{this.useNumericPagination}}/> Use numeric pagination (<code>useNumericPagination</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showPageSize\"\n      @checked={{this.showPageSize}}/> Show page size select-box (<code>showPageSize</code>)\n  </label>\n</p>\n<p>Used only for first table:</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"filteringIgnoreCase\"\n      @checked={{this.filteringIgnoreCase}}/> Do filtering ignore case (<code>filteringIgnoreCase</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"multipleColumnsSorting\"\n      @checked={{this.multipleColumnsSorting}}/> Do sorting by multiple columns (<code>multipleColumnsSorting</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"doFilteringByHiddenColumns\"\n      @checked={{this.doFilteringByHiddenColumns}}/> Do filtering by hidden columns\n    (<code>doFilteringByHiddenColumns</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showCurrentPageNumberSelect\"\n      @checked={{this.showCurrentPageNumberSelect}}/> Show dropdown for current page number (<code>showCurrentPageNumberSelect</code>)\n  </label>\n</p>\n<p class=\"form-inline\">\n  <label>\n    <Input\n      class=\"form-control\"\n      name=\"collapseNumPaginationForPagesCount\"\n      @value={{this.collapseNumPaginationForPagesCount}}/>\n    Collapse numeric pagination if pages count is greater (<code>collapseNumPaginationForPagesCount</code>)\n  </label>\n</p>\n{{yield}}', { moduleName: 'dummy/templates/components/common-table-settings-bs4.hbs' });
