import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Sort and filter by custom property\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  Filtering and sorting for \"First name\" column is done by <code>lastName</code>-property. Filtering and sorting for \"Last name\" column is done by <code>firstName</code>-property.\n</p>\n<p class=\"alert alert-info\">\n  Sorting on \"Index\" column is done by a custom sort function\n</p>\n{{! BEGIN-SNIPPET table-with-custom-sorting-and-filtering }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @multipleColumnsSorting={{false}}\n  @themeInstance={{this.fw.themeInstance}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-sorting-and-filtering.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-sorting-and-filtering.js\"/>\n  </div>\n</div>\n\n<h4>\n  Sort and filter by custom property\n  <small>server paginated table</small>\n</h4>\n<p class=\"alert alert-info\">\n  Filtering and sorting for \"First name\" column is done by <code>lastName</code>-property. Filtering and sorting for \"Last name\" column is done by <code>firstName</code>-property.\n</p>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}/>\n', { moduleName: 'dummy/templates/examples/sort-by-filter-by.hbs' });
