import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div>\n  <label for=\"custom-filter-cell-input\" class=\"emt-sr-only\">Custom filter cell input</label>\n  <Input\n    id=\"custom-filter-cell-input\"\n    @type=\"text\"\n    @value={{@column.filterString}}\n    class={{@themeInstance.input}}\n    {{on \"input\" this.updateColumnFilterString}}\n    placeholder={{@column.filterPlaceholder}}/>\n  <button\n    class=\"clearFilterIcon {{@themeInstance.clearFilterIcon}}\"\n    type=\"button\"\n    aria-label={{@themeInstance.clearFilterMsg}}\n    {{on \"click\" this.clearColumnFilterString}}>\n  </button>\n</div>\n{{yield}}\n', { moduleName: 'dummy/templates/components/filter-cell-input.hbs' });
