import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Grouped rows\n  <small>simple table</small>\n</h4>\n\n{{#if this.fw.isBs4}}\n  <GroupedRowsSettingsBs4\n    @displayGroupedValueAsToggle={{this.displayGroupedValueAsToggle}}/>\n{{/if}}\n\n{{#if this.fw.isBs5}}\n  <GroupedRowsSettingsBs4\n    @displayGroupedValueAsToggle={{this.displayGroupedValueAsToggle}}/>\n{{/if}}\n\n{{! BEGIN-SNIPPET table-with-grouped-rows }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @expandedRowComponent={{component \"expanded-row\"}}\n  @multipleExpand={{true}}\n  @multipleSelect={{true}}\n  @useDataGrouping={{true}}\n  @dataGroupProperties={{this.dataGroupProperties}}\n  @currentGroupingPropertyName={{this.currentGroupingPropertyName}}\n  @displayGroupedValueAs={{this.displayGroupedValueAs}}\n  @collapsedGroupValues={{this.collapsedGroupValues}}\n  @pageSize={{this.pageSize}}\n  @themeInstance={{this.fw.themeInstance}}\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-grouped-rows.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-grouped-rows.js\"/>\n  </div>\n</div>', { moduleName: 'dummy/templates/examples/grouped-rows.hbs' });
