import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Nested table\n  <small>simple table</small>\n</h4>\n\n{{! BEGIN-SNIPPET nested-table }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @multipleExpand={{true}}\n  @expandedRowComponent={{component \"nested-table\"}}\n  @themeInstance={{this.fw.themeInstance}}\n/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"nested-table.js\"/>\n    <CodeBlockExample @snippetName=\"nested-table.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"nested-table-component.js\"/>\n    <CodeBlockExample @snippetName=\"nested-table-component.hbs\"/>\n  </div>\n</div>\n\n<h4>\n  Nested table\n  <small>block usage</small>\n</h4>\n\n{{! BEGIN-SNIPPET nested-table-block }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @multipleExpand={{true}}\n  @themeInstance={{this.fw.themeInstance}}\nas |MT|>\n  <div class={{MT.themeInstance.headerWrapper}}>\n    <MT.GlobalFilter />\n    <MT.ColumnsDropdown />\n  </div>\n  <MT.Table as |Table|>\n    <Table.Header/>\n    <Table.Body as |Body|>\n      {{#if MT.allColumnsAreHidden}}\n        <Body.ColumnsHidden/>\n      {{else}}\n        {{#each MT.visibleContent as |record index|}}\n          <Body.Row\n            @record={{record}}\n            @index={{index}}/>\n          {{#if (includes record MT.expandedItems)}}\n            <Body.RowExpand\n              @record={{record}}\n              @index={{index}}>\n              <div class=\"emt-row\">\n                <div class=\"emt-col\">\n                  Nested table with comments belong to user\n                  <strong>{{this.record.firstName}} {{this.record.lastName}}</strong>\n                </div>\n                <div class=\"emt-col\">\n                  <ModelsTable\n                    @data={{record.comments}}\n                    @columns={{this.nestedColumns}}\n                    @multipleSelect={{true}}\n                    @isolated={{true}}\n                    @themeInstance={{this.fw.themeInstance}}\n                    @columnComponents={{hash\n                      editRow=(\n                        component\n                        (ensure-safe-component this.cellEditToggleComponent)\n                        onSaveRow=this.onSaveRow\n                        onCancelRow=this.onCancelRow\n                      )\n                    }}\n                  />\n                </div>\n              </div>\n            </Body.RowExpand>\n          {{/if}}\n        {{else}}\n          <Body.NoData/>\n        {{/each}}\n      {{/if}}\n    </Table.Body>\n  </MT.Table>\n  <MT.Footer/>\n</ModelsTable>\n{{! END-SNIPPET }}\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"nested-table-block.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"nested-table-controller.js\"/>\n  </div>\n</div>\n{{outlet}}', { moduleName: 'dummy/templates/examples/nested-table.hbs' });
