import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>Custom icons from Font Awesome</h4>\n<pre>ember install @fortawesome/ember-fontawesome</pre>\n<pre>npm i --save-dev @fortawesome/free-solid-svg-icons</pre>\n{{! BEGIN-SNIPPET custom-icons-from-font-awesome }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstanceWithCustomFa}}/>\n{{! END-SNIPPET }}\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"custom-icons-from-font-awesome.hbs\" />\n    <h5>Icons:</h5>\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"cross.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"caret.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"down.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"first.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"hide-col.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"last.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"next.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"prev.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"show-col.hbs\" />\n    <CodeBlockExample @showToggle={{false}} @snippetName=\"up.hbs\" />\n  </div>\n  <div class=\"emt-col\">\n    {{#if this.fw.isBs4}}\n      <CodeBlockExample @snippetName=\"ember-bootstrap-v4-with-custom-fa-theme-service.js\"/>\n    {{/if}}\n    {{#if this.fw.isBs5}}\n      <CodeBlockExample @snippetName=\"ember-bootstrap-v5-with-custom-fa-theme-service.js\"/>\n    {{/if}}\n    {{#if this.fw.isPlainHtml}}\n      <CodeBlockExample @snippetName=\"plain-html-with-custom-fa-theme-service.js\"/>\n    {{/if}}\n  </div>\n</div>\n{{outlet}}', { moduleName: 'dummy/templates/examples/font-awesome.hbs' });
