import { hbs } from 'ember-cli-htmlbars';
export default hbs('<tr>\n  {{#each @visibleProcessedColumns as |column index|}}\n    <td>\n      Column #{{index}}.\n      <span class=\"expanded\">{{@expandedGroupedItems.length}}</span>\n      <span class=\"visible\">{{@visibleGroupedItems.length}}</span>\n      <span class=\"selected\">{{@selectedGroupedItems.length}}</span>\n    </td>\n  {{/each}}\n</tr>', { moduleName: 'dummy/templates/components/custom-group-summary-row.hbs' });
