import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Custom component in a cell with closure actions\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  Some records may be deleted from both tables in the same time.\n</p>\n{{! BEGIN-SNIPPET table-with-custom-components }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @columnComponents={{hash\n    deleteRow=(\n      component \"delete-row-comp\"\n      onClick=this.deleteRecord\n    )\n  }}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-custom-components.hbs\"/>\n    <CodeBlockExample @snippetName=\"table-with-custom-components.js\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"delete-row-comp.hbs\"/>\n    <CodeBlockExample @snippetName=\"delete-row-comp.js\"/>\n  </div>\n</div>', { moduleName: 'dummy/templates/examples/custom-components-in-cell.hbs' });
