import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Query parameters\n</h4>\n\n{{! BEGIN-SNIPPET query-params-template }}\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @filterQueryParameters={{this.filterQueryParameters}}\n  @currentPageNumber={{this.page}}\n  @pageSize={{this.pageSize}}\n  @filterString={{this.globalSearch}}\n  @onDisplayDataChanged={{this.displayAction}}\n  @themeInstance={{this.fw.themeInstance}}\n  @doQuery={{this.doQuery}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}\n/>\n{{! END-SNIPPET }}\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"query-params-controller.js\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"query-params-template.hbs\"/>\n    <CodeBlockExample @snippetName=\"query-params-route.js\"/>\n  </div>\n</div>\n{{outlet}}', { moduleName: 'dummy/templates/examples/query-params.hbs' });
