import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showComponentFooter\"\n      @checked={{@showComponentFooter}}/> Show Footer (<code>showComponentFooter</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showColumnsDropdown\"\n      @checked={{@showColumnsDropdown}}/> Show Columns Dropdown (<code>showColumnsDropdown</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"useFilteringByColumns\"\n      @checked={{@useFilteringByColumns}}/> Use filtering by columns (<code>useFilteringByColumns</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showGlobalFilter\"\n      @checked={{@showGlobalFilter}}/> Show global filter (<code>showGlobalFilter</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"useNumericPagination\"\n      @checked={{@useNumericPagination}}/> Use numeric pagination (<code>useNumericPagination</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showPageSize\"\n      @checked={{@showPageSize}}/> Show page size select-box (<code>showPageSize</code>)\n  </label>\n</p>\n<p>Used only for first table:</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"filteringIgnoreCase\"\n      @checked={{@filteringIgnoreCase}}/> Do filtering ignore case (<code>filteringIgnoreCase</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"multipleColumnsSorting\"\n      @checked={{@multipleColumnsSorting}}/> Do sorting by multiple columns (<code>multipleColumnsSorting</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"doFilteringByHiddenColumns\"\n      @checked={{@doFilteringByHiddenColumns}}/> Do filtering by hidden columns (<code>doFilteringByHiddenColumns</code>)\n  </label>\n</p>\n<p>\n  <label>\n    <Input\n      @type=\"checkbox\"\n      name=\"showCurrentPageNumberSelect\"\n      @checked={{@showCurrentPageNumberSelect}}/> Show dropdown for current page number (<code>showCurrentPageNumberSelect</code>)\n  </label>\n</p>\n<p class=\"form-inline\">\n  <label>\n    <Input\n      class=\"form-control\"\n      name=\"collapseNumPaginationForPagesCount\"\n      @value={{@collapseNumPaginationForPagesCount}}/>\n    Collapse numeric pagination if pages count is greater (<code>collapseNumPaginationForPagesCount</code>)\n  </label>\n</p>\n{{yield}}', { moduleName: 'dummy/templates/components/common-table-settings-plain-html.hbs' });
