import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div class=\"code-block-example\">\n  {{#let (get-code-snippet @snippetName) as |snippet|}}\n    <h4>{{@snippetName}}</h4>\n    {{#if this.showToggle}}\n      <button\n        onclick={{this.toggleCodeVisibility}}\n        type=\"button\"\n        class={{this.fw.themeInstance.buttonDefault}}>\n        {{#if this.opened}}Hide{{else}}Show{{/if}} code\n      </button>\n    {{/if}}\n    {{#if this.opened}}\n      <CodeBlock\n        class=\"code-block\"\n        @code={{snippet.source}}\n        @language={{snippet.language}}/>\n    {{/if}}\n  {{/let}}\n  {{yield}}\n</div>', { moduleName: 'dummy/templates/components/code-block-example.hbs' });
