import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p class=\"alert alert-info\">Check console to get data sent to the parent context</p>\n<h4>\n  Handle user interaction with a component\n  <small>simple table</small>\n</h4>\n{{! BEGIN-SNIPPET display-data-changed-action }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @onDisplayDataChanged={{this.myAction}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"display-data-changed-action.hbs\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"display-data-changed-action.js\"/>\n  </div>\n</div>\n\n<h4>\n  Handle user interaction with a component\n  <small>server paginated table</small>\n</h4>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @themeInstance={{this.fw.themeInstance}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}\n  @doQuery={{this.doQuery}}\n  @onDisplayDataChanged={{this.myAction}}/>\n', { moduleName: 'dummy/templates/examples/display-data-changed-action.hbs' });
