import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Expandable Rows\n  <small>simple table</small>\n</h4>\n<p class=\"alert alert-info\">\n  Some column should has component with actions <code>expandRow</code> and <code>collapseRow</code>. This column should have field <code>mayBeHidden</code> set to <code>false</code>. Value for <code>expandedRowComponent</code> should be provided to the component. It should be equal to the component name shown in the expanded row. Set <code>multipleExpand</code> to <code>true</code> to allow multiple rows be expanded in the same time.\n</p>\n{{! BEGIN-SNIPPET table-with-expandable-rows }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @expandedRowComponent={{component \"expanded-row\"}}\n  @multipleExpand={{true}}\n  @themeInstance={{this.fw.themeInstance}}\n  @expandedItems={{this.expandedItems}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-expandable-rows.hbs\"/>\n    <CodeBlockExample @snippetName=\"table-with-expandable-rows.js\"/>\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"expanded-row.hbs\"/>\n    <CodeBlockExample @snippetName=\"expanded-row.js\"/>\n  </div>\n</div>', { moduleName: 'dummy/templates/examples/expandable-rows.hbs' });
