import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\n  Grouped headers\n  <small>simple table</small>\n</h4>\n{{! BEGIN-SNIPPET table-with-grouped-headers }}\n<ModelsTable\n  @data={{this.data}}\n  @columns={{this.columns}}\n  @groupedHeaders={{this.groupedHeaders}}\n  @showColumnsDropdown={{false}}\n  @showGlobalFilter={{false}}\n  @useFilteringByColumns={{false}}\n  @themeInstance={{this.fw.themeInstance}}/>\n{{! END-SNIPPET }}\n\n<div class=\"emt-row\">\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-grouped-headers.hbs\" />\n  </div>\n  <div class=\"emt-col\">\n    <CodeBlockExample @snippetName=\"table-with-grouped-headers.js\" />\n  </div>\n</div>\n\n<h4>\n  Grouped headers\n  <small>server paginated table</small>\n</h4>\n<ModelsTableServerPaginated\n  @data={{this.queuedModel}}\n  @columns={{this.columns}}\n  @groupedHeaders={{this.groupedHeaders}}\n  @showColumnsDropdown={{false}}\n  @showGlobalFilter={{false}}\n  @useFilteringByColumns={{false}}\n  @themeInstance={{this.fw.themeInstance}}\n  @itemsCount={{this.itemsCount}}\n  @pagesCount={{this.pagesCount}}\n  @doQuery={{this.doQuery}}/>\n', { moduleName: 'dummy/templates/examples/grouped-headers.hbs' });
